.fk-input {
  display: block;
  width: 100%;
  color: $snow;
  background: $raven;
  border: $border;
  border-radius: $border-radius;
  &:focus {
    color: $snow;
    background: $raven;
    border: $border-focus;
    box-shadow: none;
    outline: none;
  }
}

.fk-input-lg {
  padding: 12px 18px;
  font-size: $font-size-lg;
  font-weight: $font-weight-lg;
}
