$snow: #e8eced;
$near-black: #0e1117;
$shadow: #1a2430;
$raven: #141d27;
$stone: #818f99;
$blue: #3eb8ff;
$green: #32b877;
$yellow: #d9b100;
$red: #c71a2f;

$font-size-xs: 10px;
$font-size-sm: 12px;
$font-size-md: 14px;
$font-size-lg: 16px;
$font-size-xl: 18px;
$font-size-xxl: 24px;

$font-weight-sm: 400;
$font-weight-md: 500;
$font-weight-lg: 600;
$font-weight-xl: 700;

$font-height-default: 1;
$font-height-xl: 22px;

$border-radius: 4px;
$box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5);

$border: 1px solid $stone;
$border-focus: 1px solid $snow;
$border-blur: 1px solid rgba($stone, 0.3);

$opacity-blur: 0.8;
$opacity-default: 1;
