.fk-button-icon {
  border: none;
  color: $snow;
  background: transparent;
  transition: none;
  &:hover,
  &:focus,
  &:focus-visible {
    box-shadow: none;
    border: none;
    color: $snow;
    background: transparent;
    box-shadow: none;
    outline: none;

    i {
      color: $snow;
    }
  }

  i {
    color: $stone;
  }
}
